import BookService from '@/services/BookService.js';

export default {

    state: {
        books: null,
        booksFilterBy: null
    },

    getters: {
        booksToShow(state) {
          const books = state.books
          const booksFilterBy = state.booksFilterBy
          if (!state.booksFilterBy) return books;
          if (booksFilterBy.max === "") booksFilterBy.max = Infinity;
          const filterForRegEx = booksFilterBy.title.length
            ? `${booksFilterBy.title}\w*`
            : "";
          const filterRegEx = new RegExp(filterForRegEx, "i");
          return books?.filter((book) => {
            return (
              filterRegEx.test(book.title) &&
              book.listPrice.amount > booksFilterBy.min &&
              book.listPrice.amount < booksFilterBy.max
            );
          });
        },
    },

    mutations: {
        setBooks(state, { books }) {
            state.books = books
        },

        setBooksFilterBy(state, { filterBy }) {
          filterBy.min = !filterBy.min ? 0 : filterBy.min;
          filterBy.max = !filterBy.max ? Infinity : filterBy.max;
          state.booksFilterBy = filterBy;
        },
    },

    actions: {
        async loadBooks(context) {
          const books = await BookService.query()
          context.commit({ type: 'setBooks', books: [...books] })
        },
    },

    modules: {}
}