<template>
  <section class="book-add">
    <input
      type="text"
      placeholder="Search for a book"
      v-model="input"
      @input="onInputChange"
    />
    <ul v-for="book in searchResults" :key="book.id">
      <li>
        {{ book.volumeInfo.title.slice(0, 80) }}
        <button class="add-btn" @click="addBook(book)">+</button>
      </li>
    </ul>
  </section>
</template>

<script>
import BookService from "@/services/BookService.js";
import eventBus from "@/services/EventBusService.js";

export default {
  data() {
    return {
      input: "",
      searchResults: [],
    };
  },
  methods: {
    async onInputChange() {
      if (!this.input) return;
      const foundBooks = await BookService.getBooks(this.input);
      this.searchResults = foundBooks.items;
    },
    async loadBooks() {
      this.$store.dispatch({ type: "loadBooks" });
    },
    addBook(book) {
      this.input = "";
      this.searchResults = [];
      BookService.addBook(book);
      eventBus.$emit("closeModal");
      this.loadBooks();
    },
  },
};
</script>