<template>
  <section class="details-review">
    <div class="direction">
      <!-- <h3 @click="goToPage(-1)" class="direction">Previous Page</h3> -->
      <i class="fas fa-9x fa-caret-left" @click="goToPage(-1)"></i>
    </div>
    <div class="book-details" v-if="book">
      <div>
        <img
          class="sale-img"
          :class="isOnSale"
          src="../assets/img/sale.png"
          alt=""
        />
        <h2>{{ book.title }}</h2>
        <h3>{{ book.subtitle }}</h3>
        <img :src="book.thumbnail" alt="" />
        <p>Written by {{ authors }}</p>
        <long-text :text="book.description" :maxChar="100"></long-text>
        <p>This book is {{ oldOrNew }}</p>
        <p>Reading time - {{ readingTime }}</p>
        <p>Categories - {{ bookCategories }}</p>
        <p>
          Language - <span class="lang">{{ language }}</span>
        </p>
        <p :class="price">
          Price - {{ currencyFormat }}{{ book.listPrice.amount }}
        </p>
        <router-link to="/book">Back to list</router-link>
      </div>
    </div>

    <div v-else>Loading...</div>

    <section v-if="book" class="add-reviews-and-reviews">
      <review-add :book="book" @addedReview="addReview" />

      <div class="reviews">
        <h2>Reviews</h2>
        <div v-for="(review, idx) in book.reviews" :key="idx">
          <reviews :review="review" />
        </div>
      </div>
    </section>
    <div class="direction">
      <i class="fas fa-9x fa-caret-right" @click="goToPage(1)"></i>
      <!-- <h3  class="direction"></h3> -->
    </div>
  </section>
</template>

<script>
import BookService from "@/services/BookService.js";
import LongText from "@/cmps/LongText";
import ReviewAdd from "@/cmps/ReviewAdd";
import Reviews from "@/cmps/Reviews";

export default {
  data() {
    return {
      book: null,
      currId: "",
      routeId: "",
    };
  },
  watch: {
    async routeId() {
      this.$router.push(`/book/${this.routeId}`);
      this.loadPage();
    },
  },
  async created() {
    this.loadPage();
  },
  methods: {
    async addReview({ id, review }) {
      this.book = await BookService.addReview(id, review);
    },
    async goToPage(direction) {
      this.routeId = await BookService.getIdToGo(this.currId, direction);
    },
    async loadPage() {
      const { id } = this.$route.params;
      this.book = await BookService.getById(id);
      this.currId = id;
    },
  },
  computed: {
    authors() {
      return this.book.authors.join(", ");
    },
    oldOrNew() {
      let currYear = new Date().getFullYear();
      let publishDate = +this.book.publishedDate;
      let years = currYear - publishDate;
      let veteranOrNew;
      if (years > 10) veteranOrNew = "Veteran Book";
      else if (years < 10) veteranOrNew = "New!";
      return veteranOrNew;
    },
    readingTime() {
      let pages = this.book.pageCount;
      let readingTime;
      if (pages >= 500) readingTime = "Long reading";
      else if (pages >= 200 && pages < 500) readingTime = "Decent Reading";
      else if (pages < 200) readingTime = "Light reading";
      return readingTime;
    },
    bookCategories() {
      return this.book.categories.join(", ");
    },
    language() {
      return this.book.language;
    },
    price() {
      let price = this.book.listPrice.amount;
      let isHighPrice;
      if (price <= 20 && price > 0) isHighPrice = "green";
      else if (price >= 150) isHighPrice = "red";
      else isHighPrice = "";
      return isHighPrice;
    },
    currencyFormat() {
      let code = this.book.listPrice.currencyCode;
      let curr;
      switch (code) {
        case "ILS":
          curr = "";
          break;
        case "USD":
          curr = "$";
          break;
        case "EUR":
          curr = "";
          break;
      }
      return curr;
    },
    isOnSale() {
      return this.book.listPrice.isOnSale ? "" : "none";
    },
  },

  components: {
    LongText,
    ReviewAdd,
    Reviews,
  },
};
</script>

<style lang="scss" scoped>
.green {
  color: green;
}

.red {
  color: red;
}
</style>