<template>
  <p class="long-text">
    {{ longText }} <button class="btn" @click="toggleIsOpen">{{ see }}</button>
  </p>
</template>

<script>
export default {
  props: ["text"],

  data() {
    return {
      isLongTxtShown: false,
    };
  },

  methods: {
    toggleIsOpen() {
      this.isLongTxtShown = !this.isLongTxtShown;
    },
  },

  computed: {
    longText() {
      const lessText = this.text.slice(0, 80);
      const moreText = this.text.slice(80);
      if (this.text.length > 80)
        return this.isLongTxtShown
          ? lessText + moreText + " "
          : lessText + "... ";
      else return lessText;
    },
    see() {
      if (this.text.length > 80)
        return this.isLongTxtShown ? "See less" : "See more";
    },
  },
};
</script>