<template>
  <section class="book-filter">
    <label>Book title</label>
    <input
      autocomplete="off"
      type="text"
      name="title"
      v-model="filterBy.title"
      @input="setFilter"
      placeholder="Search a book"
    />
    <label>Price</label>
    <input
      autocomplete="off"
      type="number"
      name="min"
      min = 0
      v-model.number="filterBy.min"
      @input="setFilter"
      placeholder="Minimum price"
    />
    <span>-</span>
    <input
      autocomplete="off"
      type="number"
      name="max"
      min = 0
      v-model.number="filterBy.max"
      @input="setFilter"
      placeholder="Maximun price"
    />
  </section>
</template>

<script>
export default {
  data() {
    return {
      filterBy: {
        title: "",
        min: "",
        max: Infinity,
      },
    };
  },
  methods: {
    setFilter() {
      this.$emit("filtered", JSON.parse(JSON.stringify(this.filterBy)));
    },
  },
};
</script>