<template>
  <section class="book-app">
    <button class="add-book-btn" @click="openModal">Add new book</button>
    <book-filter @filtered="setFilter"></book-filter>
    <book-list :books="booksToShow"></book-list>
  </section>
</template>

<script>
import BookService from "@/services/BookService.js";
import BookList from "@/cmps/BookList";
import BookFilter from "@/cmps/BookFilter";
import eventBus from "@/services/EventBusService.js";

export default {
  data() {
    return {
      filterBy: {
        title: "",
        min: "",
        max: Infinity,
      },
    };
  },
  async created() {
    this.loadBooks();
    eventBus.$on("loadBooks", () => {
      this.loadBooks();
    });
  },
  methods: {
    async loadBooks() {
      this.$store.dispatch({ type: "loadBooks" });
    },
    setFilter(filterBy) {
      this.$store.commit({ type: "setBooksFilterBy", filterBy });
    },
    openModal() {
      eventBus.$emit("openModal");
    },
  },
  computed: {
    booksToShow() {
      return this.$store.getters.booksToShow;
    },
  },
  components: {
    BookList,
    BookFilter,
  },
};
</script>