<template>
  <section class="review-add">
    <h2>Add review</h2>
    <form @submit.prevent="addReview">
      <input
        ref="nameInput"
        autocomplete="off"
        type="text"
        name="name"
        v-model="review.name"
        placeholder="Your name"
      />
      <!-- <label for="when-read">Date of reading</label>
      <input type="date" name="when-read" /> -->
      <textarea name="text" rows="5" v-model="review.text"></textarea>
      <button>Add review</button>
    </form>
  </section>
</template>

<script>
import BookService from "@/services/BookService.js";

export default {
  props: ["book"],
  data() {
    return {
      review: {
        name: "",
        text: "",
      },
    };
  },
  mounted() {
    this.$refs.nameInput.focus();
  },
  methods: {
    addReview() {
      if (!this.review.name) this.review.name = "Books Reader";
      if (!this.review.text) this.review.text = "***No Text***";
      this.$emit("addedReview", { id: this.book.id, review: this.review });
      this.resetReview();
    },
    resetReview() {
      this.review = {
        name: "",
        text: "",
      };
    },
  },
};
</script>