<template>
  <section class="modal-wrapper" :class="isModalShown" @click="closeModal">
    <div class="modal-content" v-on:click.stop>
      <button class="close-modal-btn" @click="closeModal">x</button>
      <h2>Add a new book</h2>
      <book-add />
    </div>
  </section>
</template>

<script>
import eventBus from "@/services/EventBusService.js";
import BookAdd from "@/cmps/BookAdd";

export default {
  data() {
    return {
      isShown: false,
    };
  },
  created() {
    eventBus.$on("openModal", () => {
      this.isShown = true;
    });
    eventBus.$on("closeModal", () => {
      this.isShown = false;
    });
  },
  destroyed() {
    eventBus.$off("openModal");
    eventBus.$off("closeModal");
  },
  methods: {
    closeModal() {
      this.isShown = false;
    },
  },
  computed: {
    isModalShown() {
      return this.isShown ? "" : "hide";
    },
  },
  components: {
    BookAdd,
  },
};
</script>