<template>
  <section class="book-preview">
    <h3>{{ book.title.slice(0, 20) }}</h3>
    <img :src="book.thumbnail" alt="" />
    <p>Price {{ currencyFormat }}{{ book.listPrice.amount }}</p>
    <router-link :to="`/book/${book.id}`">Details</router-link>
  </section>
</template>

<script>
export default {
  props: ["book"],
  computed: {
    currencyFormat() {
      let code = this.book.listPrice.currencyCode;
      let curr;
      switch (code) {
        case "ILS":
          curr = "";
          break;
        case "USD":
          curr = "$";
          break;
        case "EUR":
          curr = "";
          break;
      }
      return curr;
    },
  },
};
</script>